table{
    width: 100%;
    height: 30vh;
    text-align: left;
    border-collapse: collapse;
}

thead {
    background-color: #00cad1;
    color: #eee;
}

th{
    padding: 10px 0;
}

th, td{
    padding-left: 5px;
}

tbody tr:nth-of-type(2n+1){
    background-color: #aaa;
}

tbody tr:nth-of-type(2n+2){
    background-color: #ddd;
}

